.container {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 80vw !important;
    margin: 0 !important;
}

.container .card {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    border: 0px;
    margin: 20px 2px 20px 2px;
    box-shadow: none;
    border-radius: 50%;
    background-color: transparent;

}

.container .card .icon {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: 0.7s;
    
}

.container .card:nth-child(1) .icon {
    background: rgba(69, 69, 69, 0.3);
    border-radius: 50%;
}

.container .card:nth-child(1) .icon:hover {
    background: rgb(238, 219, 78); 
}

.container .card:nth-child(2) .icon {
    background:rgba(69, 69, 69, 0.3);
    border-radius: 50%;
}

.container .card:nth-child(2) .icon:hover {
    background:rgb(72, 163, 205); 
}

.container .card:nth-child(3) .icon {
    background:rgba(69, 69, 69, 0.3);
    border-radius: 50%; 
}

.container .card:nth-child(3) .icon:hover {
    background: rgb(146, 200, 73); 
}

.container .card:nth-child(4) .icon {
    background: rgba(69, 69, 69, 0.3);
    border-radius: 50%; 
}

.container .card:nth-child(4) .icon:hover {
    background: rgb(152, 194, 60); 
}

.container .card:nth-child(5) .icon {
    background: rgba(69, 69, 69, 0.3);
    border-radius: 50%; 
}

.container .card:nth-child(5) .icon:hover {
    background: rgb(152, 194, 60); 
}

.container .card:nth-child(6) .icon {
    background: rgba(69, 69, 69, 0.3);
    border-radius: 50%; 
}

.container .card:nth-child(6) .icon:hover {
    background: rgb(210, 88, 53); 
}

.container .card:nth-child(7) .icon {
    background: rgba(69, 69, 69, 0.3);
    border-radius: 50%;
}

.container .card:nth-child(7) .icon:hover {
    background: rgb(44, 80, 220); 
}


.container .card .face {
    width: 70px;
    height: 70px;
    transition: 1s;
}

.container .card .face.face1 {
    position: relative;
    background: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    border-radius: 50%;
}

.container .card:hover .face.face1{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}

.container .card .face.face1 .content {
    opacity: 1;
    transition: 0.5s;
}

.container .card:hover .face.face1 .content {
    opacity: 1;
}

.container .card .face.face1 .content i{
    max-width: 20px;
}

.container .card .face.face2 {
    position: relative;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 0px;
    box-sizing: border-box;
    border-radius: 10%;
    width: 130px;
    background-color: transparent;
}

.container .card:hover .face.face2{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    box-sizing: border-box;
    box-shadow: 10px 23px 30px 0px var(--primaryBoxShadow);
}

.container .card .face.face2 .content h3 {
    margin: 0 0 0px 0;
    padding: 0;
    font-size: 12px;
    text-align: center;
} 

