  margin-top: 4vh;
  padding: 3%;
  grid-gap: 3%;
  gap: 3%;
}

.project-item {
  display: flex;
  margin: 2%;
}

.project-item:hover > .label-project {
  background-color: gray;
  -webkit-transform: translatex(50px);
          transform: translatex(50px);
  transition: 1s ease-in;
}

.label-project {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-top: 10%;
  margin-right: -2%;
  background-color:#1de49f;
  width: 40px;
  height: 180px;
  box-shadow: 1px 5px 20px 8px var(--primaryBoxShadow);
  -webkit-transform: translatex(0px);
          transform: translatex(0px);
  transition: 1s ease-in;     
}

.label-project h2{
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  font-size: 1.5em;
  color: #515151;
}

.container-project {
  position: relative;
  margin: 2%;
  height: 380px;
  width: 290px;
  overflow: hidden;
  box-shadow: 0px 2px 5px rgba(0,0,0,0.3);
  transition: box-shadow 0.3s ease-out;
}

.container-project:hover {
  box-shadow: 1px 2px 10px rgba(0,0,0,0.5);
}

.img-container-project {
  background-color: #000;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  z-index: 2;
}

.img-container-project:hover {
	cursor: pointer;
}

.container-project:hover .img-container-project {
	-webkit-transform: translateY(-110px);
	        transform: translateY(-110px);
}

.img-container-project > img {
  transition: opacity 0.3s ease-out;
  width: 100%;
  height: 100%;
}

.container-project:hover > .img-container-project > img {
  opacity: 0.5;
}

.project-more {
  display: flex;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 3;
  margin: 0;
  padding: 0;
}

.project-more > li {
  list-style: none;
}

.project-more > li > a {
  text-align: center;
  display: block;
  justify-content: center;
  align-content: center;
  margin: 0 7px;
  opacity: 0;
  -webkit-transform: translateY(200px);
          transform: translateY(200px);
  transition: all 0.3s ease-out;
}

.container-project:hover > .project-more > li > a {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

.project-more > li > a > .fa {
  font-size: 24px;
  line-height: 50px;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.project-more > li > a:hover > .fa {
  -webkit-transform: rotateY(360deg);
          transform: rotateY(360deg);
}

.container-project:hover .project-more li:nth-child(1) a {
	transition-delay: 0.3s;
}

.container-project:hover .project-more li:nth-child(2) a {
	transition-delay: 0.5s;
}

